bold;\r\n    color: white;\r\n    font-size: 1.5rem;\r\n    width: 200px;\r\n    margin: 15px;\r\n    border-radius: 25px;\r\n    border: var(--header-button-border) solid 3px;\r\n    padding: 5px;\r\n}\r\n\r\n.views button {\r\n    margin-bottom: 0;\r\n    border-bottom: none;\r\n    border-radius: 25px 25px 0 0;\r\n    padding: 10px 5px 10px;\r\n}\r\n/* Form Dialog Section */\r\n.news {\r\n    position: relative;\r\n}\r\n.new-dialog {\r\n    position: absolute;\r\n    top: 90px;\r\n    width: 300px;\r\n    padding: 10px 5px;\r\n    border: var(--header-button-border) 2px solid;\r\n    border-radius: 25px;\r\n    background-color: var(--header-button-color);\r\n    z-index: 5;\r\n}\r\n.new-dialog form {\r\n    display: flex;\r\n    flex-direction: column;\r\n}\r\n.new-dialog label {\r\n    font-weight: bold;\r\n    font-size: 1.25rem;\r\n    color: white;\r\n}\r\n.new-dialog input {\r\n    border-radius: 5px;\r\n    font-size: 1.25rem;\r\n}\r\n.dialog-buttons {\r\n    display: flex;\r\n    justify-content: space-around;\r\n    padding-top: 10px;\r\n}\r\n.dialog-buttons button {\r\n    background: none;\r\n    border: none;\r\n    color: white;\r\n    font-weight: bold;\r\n    font-size: 1.25rem;\r\n}\r\n.error-dialog {\r\n    color: white;\r\n    background-color: var(--header-button-color);\r\n    border-color: var(--header-button-border);\r\n    border-radius: 25px;\r\n    font-weight: bold;\r\n}\r\n/* Main */\r\nmain {\r\n    display: grid;\r\n    grid-template-columns: 1fr 10fr;\r\n    height: calc(100vh - 80px);\r\n}\r\n.sidebar {\r\n    background-color: var(--header-color);\r\n    width: 250px;\r\n}\r\n .sidebar p, .sidebar li, .sidebar h6 {\r\n    color: white;\r\n}\r\n.sidebar li,  .sidebar h6 {\r\n    margin-top: 20px;\r\n}\r\n.sidebar h6 {\r\n    margin-left: 5%;\r\n    font-size: 1.5rem;\r\n    font-weight: bold;\r\n}\r\n.listed-folder {\r\n    list-style: none;\r\n    font-size: 1.25rem;\r\n    padding-left: 7.5%;\r\n    display: flex;\r\n    align-ite